.home{
}

.home-banner{
  margin-bottom: 56px;
  &__text-inner{
    padding: 68px 70px;
    background-repeat: no-repeat;
    background-size: 100% auto;
  }
  &__text, &__title{
    color: #302C34;
    margin: 0;
  }
  &__text{
    font-size: 24px;
    font-weight: 400;
    max-width: 540px;
  }
  &__title{
    max-width: 450px;
    font-size: 39px;
    margin-bottom: 15px;
  }
}