.header{
  &__inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 44px;
    margin-bottom: 40px;
  }
  &__logo{

  }
  &__input-inner{
    flex: 0 1 600px;
    position: relative;
    input{
      width: 100%;
      height: 40px;
      border: 1px solid lightgrey;
      border-radius: 2px;
      padding-left: 30px;
      &:focus{
        outline: none;
      }
    }
    img{
      position: absolute;
      left: 10px;
      top: 11px;
    }
  }
  &__buttons{
    display: flex;
  }
  &__sing, &__busket{
    display: flex;
    align-items: center;
    padding: 8px 16px;
    border-radius: 2px;
    cursor: pointer;
    img{
      margin-right: 4px;
    }
    span{
    }
  }
  &__sing{
    background-color: #1890FF;
    margin-right: 10px;
    span{
      color: #fff;
    }
  }
  &__busket{
    border: 1px solid lightgrey;
    color: #595959;
  }
}

.basket{
  z-index: 11;
  left: 0;
  top: 0;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.212);
  &__block{
    max-width: 780px;
    max-height: 550px;
    width: 100%;
    height: 100%;
    padding: 40px 45px;
    background-color: #fff;
  }
  &__title-inner{
   display: flex;
   justify-content: space-between;
   align-items: center;
   img{
     cursor: pointer;
   }
  }
  &__title{
    font-size: 36px;
    margin: 0;
  } 
}