.footer{
  &__inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 156px;
    background-color: #fff;
  }
  &__first{
    display: flex;
    align-items: center;
  }
  &__logo{
    margin-right: 35px;
  }
  &__last{
    img + img{
      margin-left: 21px;
    }
  }
}

.footer-menu{
  ul{
    padding: 0;
    display: flex;
    li + li{
      margin-left: 15px;
    }
    li{
      list-style: none;
      padding: 0;
      margin: 0;
    }
  }
  &__link{
    color: #595959;
    text-decoration: none;
  }
}